*{
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}

body{
  padding: 30px;
}

input{
  color: darkgrey;
  font-size: 1.2rem;
  width: 100%;
  
}

.card-grid{
  display: flex;
  direction: row;
  flex-wrap: wrap;
  justify-content: space-around;
}

.card{
  align-content: center;
  border: 1px solid grey;
  border-radius: 6px;
  margin-bottom: 20px;
  margin-right: 10px;    
  overflow: hidden;
}

.card p{
  padding: 5px;
  text-align: center;
}

.card img{
  display: block;
  margin: auto;
  max-height: 150px;
}